<template>
  <div class="container" style="margin-top: 8vh">
    <div class="play_top">
      <div class="top_left">
        <img :src="playlist.coverImgUrl" alt="" />
      </div>

      <div class="top_right">
        <!-- 歌单标题 -->
        <p style="font-weight: bold">{{ playlist.name }}</p>
        <!-- 作者头像 -->
        <div>
          <div class="right_img">
            <img :src="playlist.creator.avatarUrl" alt="" />
          </div>
          <!-- 作者昵称 -->
          <span>{{ playlist.creator.nickname }}</span>
          <!-- 创建时间 -->
          <span>创建</span>
        </div>
        <span class="jianjie"
          >简介：
          {{ playlist.description }}
        </span>
      </div>
    </div>

    <div class="play_container">
      <el-tabs>
        <el-tab-pane label="歌曲列表" name="first">
          <div
            v-for="(item, index) in tracks"
            :key="index"
            style="height: 40px"
          >
            <!-- 歌曲 -->
            <i class="iconfont icon-bofang" @click="playMusic(item.id)"></i>
            <span
              style="width: 155px; display: inline-block; over-flow: hidden"
              >{{ item.name }}</span
            >
            <!-- 歌手 -->
            <span
              style="width: 25%; display: inline-block; text-align: center"
              >{{ item.ar[0].name }}</span
            >
            <!-- 专辑名 -->
            <span
              style="width: 25%; display: inline-block; text-align: center"
              >{{ item.al.name }}</span
            >
            <span
              style="width: 25%; display: inline-block; text-align: center"
              >{{ item.dt }}</span
            >
            <!-- 专辑图 -->
            <!-- <li>{{ item.a1.picUrl }}</li> -->
          </div>
          <div style="margin-bottom: 8vh"></div>
        </el-tab-pane>
        <el-tab-pane label="最新评论" name="second">
          <ul v-for="(item, index) in comments" :key="index" class="Line">
            <!-- 头像 -->
            <li class="play_img">
              <img
                :src="item.user.avatarUrl"
                style="height: 80%; display: inline-block"
                alt=""
              />
            </li>
            <!-- 评论者昵称 -->
            <li style="font-weight: bold" class="play_nick">
              {{ item.user.nickname }}:
            </li>

            <!-- 评论 -->
            <li class="play_ping">{{ item.content }}</li>
          </ul>
          <el-pagination
            @current-change="handleCurrentChange"
            background
            layout="prev, pager, next"
            :total="total"
            :current-page="page"
          ></el-pagination>
          <div style="margin-bottom: 8vh"></div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
// 导入 axios
import axios from "axios";
export default {
  name: "playlists",
  data() {
    return {
      activeIndex: "1",
      // 总条数
      total: 0,
      // 页码
      page: 1,
      // 歌单详情数据
      // tracks 歌曲列表
      tracks: [],
      playlist: [],
      musiclist: [],
      // 热门评论
      hotComment: [],
      // 热门评论的个数
      hotCount: 0,
      // 普通评论
      comments: [],
      avatarUrl: [],
    };
  },
  created() {
    // 获取歌单详情
    axios({
      url: "https://autumnfish.cn/playlist/detail",
      method: "get",
      params: {
        id: this.$route.query.q,
      },
    }).then((res) => {
      // 计算歌曲时间

      // console.log(res);
      console.log(res.data.playlist.tracks);
      this.playlist = res.data.playlist;
      this.tracks = res.data.playlist.tracks;
      for (let i = 0; i < this.tracks.length; i++) {
        let min = parseInt(this.tracks[i].dt / 1000 / 60);
        let sec = parseInt((this.tracks[i].dt / 1000) % 60);
        if (min < 10) {
          min = "0" + min;
        }
        if (sec < 10) {
          sec = "0" + sec;
        }
        // console.log(min + "|" + sec);
        this.tracks[i].dt = min + ":" + sec;
      }
    });
    // 获取 评论
    // axios({
    //   url: "https://autumnfish.cn/comment/hot",
    //   method: "get",
    //   params: {
    //     id: this.$route.query.q,
    //     // 传递类型
    //     type: 2,
    //   },
    // }).then((res) => {
    //   // console.log(res)
    //   this.hotComment = res.data.hotComments;
    //   // 保存个数
    //   this.hotCount = res.data.total;
    // });

    // 获取 最新评论
    axios({
      url: "https://autumnfish.cn/comment/playlist",
      method: "get",
      params: {
        id: this.$route.query.q,
        limit: 8,
        offset: 0,
      },
    }).then((res) => {
      // console.log(res)
      // 总个数
      this.total = res.data.total;
      // 评论数据
      this.comments = res.data.comments;
    });
  },
  methods: {
    playMusic(id) {
      axios({
        url: "https://autumnfish.cn/song/url",
        method: "get",
        params: {
          id, // id:id
        },
      }).then((res) => {
        // console.log(res)
        let url = res.data.data[0].url;
        // console.log(this.$parent.musicUrl)
        // 设置给父组件的 播放地址
        this.$parent.musicUrl = url;
      });
    },
    // playMV(id) {
    //   this.$router.push(`/mv?q=${id}`);
    // },
    // playMV: function (mvid) {
    //   var that = this;
    //   axios
    //     .get("https://autumnfish.cn/mv?q=" + mvid)
    //     .then(function (response) {
    //       console.log(response.data.data.url);
    //       that.mvUrl = response.data.data.url;
    //       that.isShow = true;
    //     });
    // },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
      // 保存页码
      this.page = val;
      // 重新获取数据
      axios({
        url: "https://autumnfish.cn/comment/playlist",
        method: "get",
        params: {
          id: this.$route.query.q,
          limit: 8,
          // 根据页码计算
          offset: (this.page - 1) * 8,
        },
      }).then((res) => {
        // console.log(res)
        // 总个数
        this.total = res.data.total;
        // 评论数据
        this.comments = res.data.comments;
      });
    },
  },
};
</script>

<style>
.play_ping {
  width: 80%;
  float: right;
}

.play_nick {
  display: inline-block;
  width: 10%;
  margin-top: 10px;
}
.Line li {
  height: 100px;
  display: inline-block;
}
.Line {
  height: 100px;
  margin-bottom: 10px;

  border-bottom: 1px solid black;
}
.play_container {
  width: 80%;
  margin-left: 10%;
  height: 50vh;
  color: black;
}
.jianjie {
  display: inline-block;
  width: 80%;
  height: 150px;
  overflow: hidden;
  float: right;
  font-size: 14px;
  margin-top: -50px;
}
.top_right div span {
  display: inline-block;
  width: 200px;
  margin-left: -50px;
  text-align: center;
}
.top_right div {
  width: 10%;
  height: 80px;
  margin-left: 15px;
}
.top_right p {
  display: inline-block;
  border-bottom: 1px solid black;
  padding: 5px;
  width: 100%;
  height: 20px;
  margin-top: 40px;
}
.top_right {
  width: 70%;
  display: inline-block;
  height: 100%;
  color: black;
  float: right;
}
.top_right img {
  /* background-color: #ccc; */

  width: 50px;
  height: 50px;
  display: inline-block;
  line-height: 100px;
  border-radius: 50%;
  margin-top: 20px;
  margin-left: 10px;
}
.top_left {
  width: 30%;
  height: 100%;
  /* background-color: #ccc; */
  display: inline-block;
}
.top_left img {
  width: 200px;
  padding: 35px;
  border-radius: 55px;
}
.play_top {
  width: 80%;
  margin-left: 10%;
  height: 35vh;
  /* background-color: pink; */
}
.playlist {
  width: 100%;
  /* margin-left: 15%; */
  margin-top: 8vh;
}
</style>
