<template>
  <div class="container" style="margin-top: 8vh">
    <div class="tab-container">
      <!-- tab栏 顶部 -->
      <div class="tab-bar">
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '全部' }"
          @click="(tag = '全部'), test()"
        >
          全部
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '欧美' }"
          @click="tag = '欧美'"
        >
          欧美
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '华语' }"
          @click="tag = '华语'"
        >
          华语
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '流行' }"
          @click="tag = '流行'"
        >
          流行
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '说唱' }"
          @click="tag = '说唱'"
        >
          说唱
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '摇滚' }"
          @click="tag = '摇滚'"
        >
          摇滚
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '民谣' }"
          @click="tag = '民谣'"
        >
          民谣
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '电子' }"
          @click="tag = '电子'"
        >
          电子
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '轻音乐' }"
          @click="tag = '轻音乐'"
        >
          轻音乐
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '影视原声' }"
          @click="tag = '影视原声'"
        >
          影视原声
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == 'ACG' }"
          @click="tag = 'ACG'"
        >
          ACG
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '怀旧' }"
          @click="tag = '怀旧'"
        >
          怀旧
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '治愈' }"
          @click="tag = '治愈'"
        >
          治愈
        </span>
        <span
          tabindex="1"
          class="item"
          :class="{ active: tag == '旅行' }"
          @click="tag = '旅行'"
        >
          旅行
        </span>
      </div>

      <div style="width: 90%; margin-left: 5%">
        <el-row :gutter="20">
          <el-card shadow="always">
            <el-col :span="4" v-for="(item, index) in list" :key="index">
              <el-card
                shadow="hover"
                style="margin-bottom: 10px; background-color: "
              >
                <div
                  class="grid-content bg-purple"
                  @click="toPlaylist(item.id)"
                >
                  <img :src="item.coverImgUrl" :title="item.name" alt="" />
                  <div
                    style="
                      font-size: 14px;
                      color: black;
                      overflow: hidden;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                      font-size: 12px;
                      font-weight: bold;
                    "
                  >
                    {{ item.name }}
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-card>
        </el-row>
      </div>
    </div>
    <!-- 分页器
      total 总条数
      current-page 当前页
      page-size 每页多少条数据
      current-change 页码改变事件
     -->
    <div class="test">
      <el-pagination
        @current-change="handleCurrentChange"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="10"
      ></el-pagination>
    </div>
  </div>
</template>
<style scoped>
.test {
  width: 100%;
  height: 100px;
  text-align: center;
}
.gedan img:hover {
  transform: scale(1.05);
}
.tuijian {
  width: 80%;
  margin-left: 10%;
}
.gedan {
  width: 20vh;
  display: inline-block;
  margin: 10px 10px;
  overflow: hidden;
}
.gedan span {
  width: 100%;
  height: 30px;
  display: inline-block;
  font-size: 15px;
  background-color: rgb(27, 26, 26);
}
.gedan img {
  width: 100%;
  height: 85%;
  border-radius: 10px;
  margin-top: 30px;
}
.tab-bar span {
  display: inline-block;
  margin-left: 26px;
}
.tab-bar span:focus {
  color: rgb(252, 0, 0);
  font-weight: bold;
}
.tab-bar {
  width: 80%;
  color: black;
  line-height: 50px;
  margin-left: 10%;
}
.container {
  width: 85%;
  margin-left: 14%;
}
</style>
<script>
// 导入 axios
import axios from "axios";
export default {
  name: "recommend",
  data() {
    return {
      // 总条数
      total: 0,
      // 页码
      page: 1,
      // 顶部的推荐歌单
      topList: {},
      // 歌单列表
      list: [],
      // 当前选择的数据
      tag: "全部",
    };
  },
  // 侦听器
  watch: {
    tag() {
      // 顶部的 精品歌单
      this.topData();
      // 歌单列表
      this.listData();
      // 修改页码为1
      this.page = 1;
    },
  },
  created() {
    // 顶部的 精品歌单
    this.topData();
    // 歌单列表
    this.listData();
  },
  methods: {
    // 去歌单详情页
    toPlaylist(id) {
      // 跳转并携带数据即可
      this.$router.push(`/playlists?q=${id}`);
    },

    // 抽取的方法1 顶部的数据
    topData() {
      axios({
        url: "https://autumnfish.cn/top/playlist/highquality",
        method: "get",
        params: {
          limit: 1,
          // 分类数据
          cat: this.tag,
        },
      }).then((res) => {
        // console.log(res);
        this.topList = res.data.playlists[0];
      });
    },
    // 抽取的方法2 列表数据
    //歌单
    listData() {
      axios({
        url: "https://autumnfish.cn/top/playlist/",
        method: "get",
        params: {
          limit: 12,
          // 起始的值 （页码-1）*每页多少条数据
          offset: (this.page - 1) * 10,
          // 分类数据
          cat: this.tag,
        },
      }).then((res) => {
        // console.log(res);
        // 保存总条数
        this.total = res.data.total;
        // 保存数据
        this.list = res.data.playlists;
      });
    },
    // 页码改变事件
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`)
      // 保存页码
      this.page = val;
      // 重新获取数据
      this.listData();
    },
  },
};
</script>
