<template>
  <div class="result-container">
    <div class="con">
      <el-card shadow="always" style="margin-bottom: 5vh">
        <div class="title-wrap">
          <!-- 标题 -->
          <h2 class="title">{{ $route.query.q }}</h2>
          <span class="sub-title">找到 {{ count }} 个结果</span>
        </div>

        <el-tabs v-model="activeIndex">
          <!-- 歌曲 -->
          <el-tab-pane label="歌曲" name="songs">
            <table class="el-table" style="background-color: #f1f3f4">
              <tbody>
                <tr
                  style="background-color: #f1f3f4"
                  v-for="(item, index) in songList"
                  :key="index"
                  class="el-table__row"
                  @click="playMusic(item.id)"
                >
                  <td>
                    <div class="song-wrap">
                      <div class="name-wrap" style="">
                        <!-- 歌名 -->
                        <span>{{ item.name }}</span>
                      </div>
                      <!-- 二级标题 -->
                      <span v-if="item.alias.length != 0">{{
                        item.alias[0]
                      }}</span>
                    </div>
                  </td>
                  <td>{{ item.artists[0].name }}</td>
                  <td>{{ item.album.name }}</td>
                  <td>{{ item.duration }}</td>
                </tr>
              </tbody>
            </table>
          </el-tab-pane>
          <!-- 歌单 -->
          <el-tab-pane label="歌单" name="lists">
            <!-- <div
              v-for="(item, index) in playlists"
              :key="index"
              class="songs"
              @click="toPlaylist(item.id)"
            >
              <img :src="item.coverImgUrl" alt="" />
              <p class="name">{{ item.name }}</p>
            </div> -->
            <el-col :span="6" v-for="(item, index) in playlists" :key="index">
              <el-card
                shadow="hover"
                style="margin-bottom: 10px; background-color: "
              >
                <div
                  class="grid-content bg-purple"
                  @click="toPlaylist(item.id)"
                >
                  <img :src="item.coverImgUrl" alt="" />
                  <div
                    style="
                      font-size: 14px;
                      color: black;
                      overflow: hidden;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                      font-size: 12px;
                      font-weight: bold;
                    "
                  >
                    {{ item.name }}
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-tab-pane>
          <!-- MV -->
          <el-tab-pane label="MV" name="mv">
            <div
              class="result_MV"
              v-for="(item, index) in mv"
              :key="index"
              @click="toMV(item.id)"
            >
              <img :src="item.cover" alt="" />
              <div class="name">{{ item.name }}</div>
              <div class="singer">{{ item.artistName }}</div>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-card>
    </div>
  </div>
</template>

<script>
// 导入 aixos
import axios from "axios";
export default {
  name: "result",
  data() {
    return {
      // tab切换时 会改变的数据
      activeIndex: "songs",
      // 保存 查询歌曲
      songList: [],
      // 保存歌单的字段
      playlists: [],
      // 保存mv的字段
      mv: [],
      // 搜索结果的总数
      count: 0,
    };
  },
  // 生命周期钩子 回调函数
  created() {
    axios({
      url: "https://autumnfish.cn/search",
      method: "get",
      params: {
        keywords: this.$route.query.q,
        type: 1,
        // 获取的数据量
        limit: 10,
      },
    }).then((res) => {
      console.log(res);
      this.songList = res.data.result.songs;
      // 计算歌曲时间
      for (let i = 0; i < this.songList.length; i++) {
        let min = parseInt(this.songList[i].duration / 1000 / 60);
        let sec = parseInt((this.songList[i].duration / 1000) % 60);
        if (min < 10) {
          min = "0" + min;
        }
        if (sec < 10) {
          sec = "0" + sec;
        }
        // console.log(min + '|' + sec)
        this.songList[i].duration = min + ":" + sec;
      }
      // 保存总数
      this.count = res.data.result.songCount;
    });
  },
  // 侦听器
  watch: {
    activeIndex() {
      // songs 歌曲
      // lists 歌单
      // mv    mv
      let type = 1;

      // 获取个数
      let limit = 8;
      switch (this.activeIndex) {
        case "songs":
          type = 1;
          limit = 10;
          break;
        case "lists":
          type = 1000;
          limit = 8;
          break;
        case "mv":
          type = 1004;
          limit = 8;
          break;

        default:
          break;
      }

      axios({
        url: "https://autumnfish.cn/search",
        method: "get",
        params: {
          keywords: this.$route.query.q,
          type, // type:type,
          // 获取的数据量
          limit, // limit: limit
        },
      }).then((res) => {
        console.log(res);
        // 获取歌曲
        if (type == 1) {
          // 歌曲
          this.songList = res.data.result.songs;
          // 计算歌曲时间
          for (let i = 0; i < this.songList.length; i++) {
            let min = parseInt(this.songList[i].duration / 1000 / 60);
            let sec = parseInt((this.songList[i].duration / 1000) % 60);
            if (min < 10) {
              min = "0" + min;
            }
            if (sec < 10) {
              sec = "0" + sec;
            }
            // console.log(min + '|' + sec)
            this.songList[i].duration = min + ":" + sec;
          }
          // 保存总数
          this.count = res.data.result.songCount;
        }
        // 获取 歌单
        else if (type == 1000) {
          // 歌单的逻辑
          this.playlists = res.data.result.playlists;
          // 总数
          this.count = res.data.result.playlistCount;
          // 处理 播放次数
          for (let i = 0; i < this.playlists.length; i++) {
            if (this.playlists[i].playCount > 100000) {
              this.playlists[i].playCount =
                parseInt(this.playlists[i].playCount / 10000) + "万";
            }
          }
        } else {
          // 保存mv数据
          this.mv = res.data.result.mvs;
          // 总数
          this.count = res.data.result.mvCount;
          // 处理数据
          for (let i = 0; i < this.mv.length; i++) {
            // 时间
            let min = parseInt(this.mv[i].duration / 1000 / 60);
            let sec = parseInt((this.mv[i].duration / 1000) % 60);
            if (min < 10) {
              min = "0" + min;
            }
            if (sec < 10) {
              sec = "0" + sec;
            }
            this.mv[i].duration = min + ":" + sec;

            // 播放次数
            if (this.mv[i].playCount > 100000) {
              this.mv[i].playCount =
                parseInt(this.mv[i].playCount / 10000) + "万";
            }
          }
        }
      });
    },
  },
  // 方法
  methods: {
    // 去mv详情页
    toMV(id) {
      this.$router.push(`/mv?q=${id}`);
    },
    // 去歌单详情页
    toPlaylist(id) {
      // 跳转并携带数据即可
      this.$router.push(`/playlists?q=${id}`);
    },
    playMusic(id) {
      axios({
        url: "https://autumnfish.cn/song/url",
        method: "get",
        params: {
          id, // id:id
        },
      }).then((res) => {
        console.log(res);
        //  let url = res.data.data[0].url;
        // console.log(this.$parent.musicUrl);
        // 设置给父组件的 播放地址
        // this.$parent.musicUrl = url;
      });
    },
  },
};
</script>

<style>
.songs .name {
  width: 100%;
  height: 35px;
  font-size: 12px;
  overflow: hidden;
}
.songs img {
  width: 100%;
  height: 100%;
}
.songs {
  width: 150px;
  margin: 30px 42px 10px;
  display: inline-block;
}
.result_MV img {
  width: 100%;
}
.result_MV {
  font-size: 12px;
  width: 20%;
  height: 22vh;
  overflow: hidden;
  margin: 25px;
  display: inline-block;
  padding: 2px;
}
.name-warp span:hover {
  cursor: pointer;
}
.el-table__row {
  font-size: 16px;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #f1f3f4;
  border-bottom: 1px solid #ccc;
}
.con {
  width: 90%;
  margin-left: 5%;
}
.result-container {
  width: 85%;
  float: right;
  margin-top: 9vh;
}
</style>
